export default{
	props:{
		resdata:Array,
		swiperStyle:String,
		swiperItemStyle:String,
		height:{
			type:Number,
			default:380
		},
		circular:{
			type:Boolean,
			default:true
		},
		preview:{
			type:Boolean,
			default:false
		}
	},
	methods:{
		event(item,index){
			
			if(this.preview){
				return uni.previewImage({
					current:index,
					urls:this.getUrls,
					indicator:"default"
				})
			}
		}
	},
	computed:{
		getStyle(){
			return `height:${this.height}rpx`
		},
		getUrls(){
			return this.resdata.map(v=>{
				return v.src
			})
		}
	}
}
